* {
   margin: 0;
   box-sizing: border-box;
}
#root {
   width: 100%;
}

.app {
   display: flex;
   align-items: center;
   flex-direction: column;
   margin: 80px auto;
   width: 98vw;
   height: 98vw;
   max-width: 690px;
   max-height: 600px;
   box-shadow: 0 30px 0 0 seagreen;
   background-color: seagreen;
   font-family: sans-serif;
}

.box {
   position: relative;
   width: 86vw;
   height: 86vw;
   max-width: 600px;
   max-height: 600px;
   overflow: hidden;
   border: 1px solid #fff;
   background: #7ee67e;
   background-image: linear-gradient(
         rgba(255, 255, 255, 0.7) 0.1em,
         transparent 0.1em
      ),
      linear-gradient(90deg, rgba(255, 255, 255, 0.7) 0.1em, transparent 0.1em);
   background-size: 4% 4%;
   background-position: -1px -1px;
}
.dot,
.food {
   position: absolute;
   width: 4%;
   height: 4%;
   border: 1px solid #fff;
}
.dot {
   background-color: #000;
   z-index: 1;
   border: 1px solid #fff;
}
.food {
   color: #f00;
   display: flex;
   justify-content: center;
   align-content: center;
}
.food svg {
   width: 100%;
   height: 100%;
   font-size: 130%;
}

.start-screen {
   position: absolute;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: space-between;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -60%);
   z-index: 2;
   width: 50vw;
   height: 70vw;
   max-width: 300px;
   max-height: 400px;
   border-radius: 16px;
   border: 1px solid #080;
   background-color: #90ee90;
   opacity: 0.95;
   font-size: 40px;
   color: #fff;
   text-shadow: 2px 2px #000;
}
.wrapper {
   margin-top: 20%;
   display: flex;
   width: 100%;
}
.current-score,
.highest-score {
   display: flex;
   flex-direction: column;
   align-items: center;
   flex-grow: 1;
   text-align: center;
}
.current-score svg {
   color: #f00;
}

.highest-score svg {
   color: #ff0;
}

.play {
   width: 70%;
   height: 40px;
   background-color: #15c;
   color: #fff;
   margin-bottom: 10%;
   cursor: pointer;
   border: 0;
   border-radius: 12px;
   font-size: 20px;
}
.play svg {
   margin-right: 2%;
}

.play:hover {
   background-color: #0f4095;
}

.score {
   margin: left;
   width: 86vw;
   max-width: 600px;
   position: relative;
   z-index: 3;
   font-size: 30px;
   line-height: 45px;
   color: #fff;
   text-shadow: 2px 2px #000;
}

.score svg {
   color: #f00;
   margin-right: 5px;
}
